'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import {
  Box,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Avatar,
  Divider,
  Paper,
} from '@mui/material';
import { Pets, Person, Logout, ChevronRight } from '@mui/icons-material';




export default function AccountPage() {
  const router = useRouter();
  const { user, isLoading } = useUserProfile();

  const handleLogout = async () => {
    try {
      const response = await fetch('/api/auth/logout', { method: 'POST' });
      if (response.ok) {
        router.push('/login');
      }
    } catch (error) {
      console.error('Error logging out:', error);

      window.location.href = '/api/auth/logout';
    }
  };

  const menuItems = [
    {
      id: 'preferences',
      label: 'My Pet Preferences',
      icon: <Pets />,
      onClick: () => router.push('/account/preferences'),
    },
    {
      id: 'profile',
      label: 'My Profile',
      icon: <Person />,
      onClick: () => router.push('/account/profile'),
    },
    {
      id: 'logout',
      label: 'Logout',
      icon: <Logout />,
      onClick: handleLogout,
      isDanger: true,
    },
  ];

  if (isLoading) {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Loading...</Typography>
      </Box>
    );
  }

  if (!user) {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Unable to load user profile</Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ maxWidth: 600, mx: 'auto', p: 2 }}>
      <Box textAlign="center" mb={3}>
        <Typography variant="h4" fontWeight="bold" gutterBottom>
          Account
        </Typography>
      </Box>

      <Paper sx={{ p: 3, mb: 3, textAlign: 'center' }}>
        <Avatar
          sx={{
            width: 80,
            height: 80,
            mx: 'auto',
            mb: 2,
            bgcolor: '#ed9426',
            fontSize: '2rem',
          }}
        >
          {user.name?.charAt(0)?.toUpperCase() || 'U'}
        </Avatar>

        <Typography variant="h5" fontWeight="bold" gutterBottom>
          {user.name || 'User'}
        </Typography>

        <Typography variant="body2" color="text.secondary" gutterBottom>
          {user.userType === 'petOwner' ? 'Pet Owner' : 'Pet Adopter'}
        </Typography>

        {user.email && (
          <Typography variant="body2" color="text.secondary">
            {user.email}
          </Typography>
        )}
      </Paper>

      <Paper>
        <List disablePadding>
          {menuItems.map((item, index) => (
            <Box key={item.id}>
              <ListItem
                onClick={item.onClick}
                sx={{
                  cursor: 'pointer',
                  py: 2,
                  '&:hover': {
                    backgroundColor: 'action.hover',
                  },
                  ...(item.isDanger && {
                    color: 'error.main',
                    '&:hover': {
                      backgroundColor: 'error.light',
                      color: 'error.dark',
                    },
                  }),
                }}
              >
                <ListItemIcon
                  sx={{
                    color: item.isDanger ? 'error.main' : 'inherit',
                    minWidth: 40,
                  }}
                >
                  {item.icon}
                </ListItemIcon>

                <ListItemText
                  primary={item.label}
                  primaryTypographyProps={{
                    fontWeight: 500,
                  }}
                />

                {!item.isDanger && <ChevronRight color="action" />}
              </ListItem>

              {index < menuItems.length - 1 && <Divider />}
            </Box>
          ))}
        </List>
      </Paper>
    </Box>
  );
}
